import _ from 'lodash'

export default{
	data(){
		return{
			page: +this.$route.query.page || 1,//дефолтная страница. Обращение к квери-параметру текущего роута
			pageSize: 2, //макс. кол-во. эл. на странице
			pageCount: 0, //кол-во. страниц
			allItems: [], //все данные, загруженные с базы
			items: [] //отолько те данные, которые нужно показывать
		}
	},
	methods: {
		setupPagination(allItems){//пинимает все данные, загруженные с базы
			this.allItems = _.chunk(allItems, this.pageSize) // "_" - объект библиотеки lodash, "chunk" - его метод
																									//делит массив на несколько частей, возвращает массив с массивами
			this.pageCount = _.size(this.allItems) //".size" возвращает длину массива
			this.items = this.allItems[this.page - 1] || this.allItems[0]
		},
	 pageChangeHandler(page){
	 		this.$router.push(`${this.$route.path}?page=${page}`)//добавление query-параметра в адресную строку
	 		this.items = this.allItems[page - 1] || this.allItems[0]
      console.log(page)
    }
	}

}